.promo {
  background-color: $color-tangaroa;
  background-image: url("../img/content/promo/background-desktop.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: 0 0;

  @include retina {
    background-image: url("../img/content/promo/background-desktop@2x.jpg");
  }

  @include vp-1023 {
    background-image: url("../img/content/promo/background-tablet.jpg");

    @include retina {
      background-image: url("../img/content/promo/background-tablet@2x.jpg");
    }
  }

  @include vp-769 {
    background-image: url("../img/content/promo/background-mobile.jpg");

    @include retina {
      background-image: url("../img/content/promo/background-mobile@2x.jpg");
    }
  }
}

.promo__wrapper {
  padding: 305px 725px 280px 475px;
  display: flex;
  justify-content: center;

  @include vp-1023 {
    padding: 180px 50px 205px;
  }

  @include vp-769 {
    padding: 150px 25px 190px;
  }
}

.promo__content-wrapper {
  min-width: 720px;

  @include vp-1023 {
    min-width: unset;
  }

  h1 {
    margin-bottom: 32px;
    font-family: $ff-roboto-condensed;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $color-alice-blue;

    @include vp-1023 {
      margin-bottom: 0;
      font-size: 64px;
      line-height: 75px;
    }

    @include vp-769 {
      margin-bottom: 8px;
      font-size: 36px;
      line-height: 42px;
    }
  }

  p {
    margin-bottom: 46px;
    padding-right: 110px;
    font-size: 22px;
    line-height: 34px;
    color: $color-alice-blue;

    @include vp-1023 {
      margin-bottom: 57px;
      padding-right: 0;
    }

    @include vp-769 {
      margin-bottom: 44px;
      font-size: 14px;
      line-height: 22px;
    }
  }
}

.promo_button {
  justify-content: center;
  width: 410px;
  padding: 17px 26px;
  font-family: $ff-roboto-condensed;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  @include vp-1023 {
    margin-left: 45px;
  }

  @include vp-769 {
    width: 100%;
    margin-left: 0;
    padding: 12px 26px;
    font-size: 14px;
    line-height: 16px;
  }

  span {
    &:last-child {
      display: none;
    }
  }

  @include vp-769 {
    span {
      &:first-child {
        display: none;
      }

      &:last-child {
        display: inline;
      }
    }
  }
}
